import React from 'react'
import { useState } from 'react'
import axios from 'axios'
import { useContext } from 'react'
import { UserContext } from '../../../store/UserContext'
import { useParams } from 'react-router-dom'
import { useRef } from 'react'
import { useEffect } from 'react'
import { TranslationContext } from '../../../store/TranslationContext'

const FollowSection = ({data}) => {
    const [toggleFollowerLoading, setToggleFollowerLoading] = useState(false)
    const {token} = useContext(UserContext).userData
    const {username} = useParams()
    const [isFollower, setIsFollower] = useState(false)
    useEffect(()=>{
        if (data) setIsFollower(data.is_follower)
    }, [data])
    const toggleFollower=()=>{
        setToggleFollowerLoading(true)
        axios.post(`/api/user/profile/${username}/toggle-follower/`,
        {}, 
        {headers : {
                "Content-Type": "multipart/form-data",
                Authorization : 'Bearer ' + token
            }
        }
        ).then((response)=>{
            setToggleFollowerLoading(false)
            setIsFollower(response.data)
        }).catch((err)=>{
            setToggleFollowerLoading(false)
        })
    }
    const {translate}=useContext(TranslationContext)
    return (
        <button onClick={toggleFollower} disabled={toggleFollowerLoading} className="btn btn-success m-2" >
            {data && !isFollower && <><span className='mx-1'>{translate('Follow')}</span><i className="fa-solid fa-user-plus"></i></>}
            {data && isFollower && <><span className='mx-1'>{translate('Unfollow')}</span><i className="fa-solid fa-xmark mx-1"></i></>}
            {!data && <><div className='mx-1' style={{width: 80, height: 24}}></div></>}
        </button>
    )
}

export default FollowSection