import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import styled from 'styled-components'
import LazyImage from '../../Components/UI/LazyImage'
import axios from 'axios'
import { useContext } from 'react'
import {UserContext} from '../../store/UserContext'
import Error404 from '../Error404'
import FollowSection from './ProfilePage/FollowSection'
import UserProfileParameters from './ArticlePage/UserProfileParameters'
import { copyText } from '../../utils'
import { TranslationContext } from '../../store/TranslationContext'
import { BrowserContext } from '../../store/BrowserContext'

const StyledComponent = styled.div`

.upper{
    height: 100px;
    background-color: #BBBBBB;
}
.user{
    transform: translateY(-50%);
    margin-bottom: -30px;
}
.square{
    width: 100px;
    margin: auto;
}
`

const UserInfo = ({setError, setLoading}) => {
    const [data, setData] = useState(null)
    const username = useParams().username.replace('@', '')
    const {token, id} = useContext(UserContext).userData
    const userUsername = useContext(UserContext).userData.username
    let config ={}
    token ?  config = {
        headers : {
          Authorization : 'Bearer ' + token
        }
    }: config = {}
    useEffect(()=>{
        setLoading(true)
        axios.get(
            `/api/user/profile/${username.toLocaleLowerCase()}/`,
            config
            ).then((response)=>{
                setData(response.data)
                setLoading(false)
            }).catch((error)=>{
                setError(error)
                setLoading(false)
            })
    }, [])
    const {translate} =useContext(TranslationContext)
    const {setTitle, setDescription} = useContext(BrowserContext)
    useEffect(()=>{  
      if( data){
        setTitle(`${ data.name.text ||data.name } | ${translate('Ch7al Machya')}`) // Translate this
        setDescription(`${ data.name.text ||data.name } - @${data.username} ${translate('is on Ch7al machya')}`)
      }
    },[data])
  return (
    <StyledComponent className="d-flex justify-content-center align-items-center">                    
        <div className='col-12 bg-white'>
            <div className="upper">
                <UserProfileParameters/>
            </div>
            <div className="user bg-transparent">
                <div className="profile square">
                    <LazyImage src={data && data.image || '' } className={`${ !data ? 'bg-color-grey': 'bg-white'} ${ data ? data.is_page ? '' : 'rounded-circle' : 'rounded-circle'} border`} />
                </div>
            </div>
            <div className="text-center center-all">
                { data ? <h4 className="mb-0">{ data.name.text ||data.name} {data.name.is_verified && <img width='20' src='/static/others/verified.png'/>}</h4> : <div className='bg-color-grey rounded m-auto' style={{width:200, height: 30}}></div>}
                { data && <h5 className='text-center fw-bold text-grey mb-3 cursor-pointer mt-1' onClick={()=>copyText(window.location.href)}>@{ data.username }<i className="fa-regular fa-copy mx-1"></i></h5>}
                { data ? <p className="text-muted d-block mb-0 px-2">{data.bio}</p> : <div className='bg-color-grey m-auto rounded mt-3' style={{width:200, height:50}}></div>}
                { id &&  username !== userUsername &&
                <>
                    <FollowSection data={data}/>
                    <Link to={`/messages/${ data && data.id }/`} className={`btn btn-success m-2 ${!data ? 'color-theme' : ''}`} >
                        <span className='mx-1'>{translate('Message')}</span>
                        <i className="fa-brands fa-facebook-messenger mx-1"></i>
                    </Link>
                </>
                }
                <div className="d-flex justify-content-center gap-5 align-items-center my-3 px-4">
                <div className="stats">
                    <h6 className="mb-0 small">{translate('Followers')}</h6>
                    <span className='small'>{data ? data.followers_count : translate('Loading...')}</span>
                    </div>
                    <div className="stats">
                        <h6 className="mb-0 small">{translate('Articles')}</h6>
                        <span className='small'>{data ? data.articles_count : translate('Loading...')}</span>
                    </div>
                    <div className="stats">
                        <h6 className="mb-0 small">{translate('Rank')}</h6>
                        <span className='small'>{data ? (data.rank !== null ? data.rank : '-') : translate('Loading...')}</span>
                    </div>
                </div>
            </div>
        </div>
    </StyledComponent>
  )
}

export default UserInfo