import React, {lazy, useContext, useRef, useState} from 'react'
import UserInfo from './UserInfo'
import UserArticles from './UserArticles'
import Error404 from '../Error404'
import { BrowserContext } from '../../store/BrowserContext'
import { TranslationContext } from '../../store/TranslationContext'
import SuspenseComponent from '../../Components/SuspenseComponent'

const UserAbout = <SuspenseComponent Component={lazy(()=>import('./UserAbout'))} />

const ProfilePage = () => {
  const [error, setError] = useState(null)
  const [loading, setLoading] = useState(true)
  const [showArticles, setShowArticles] = useState(true)
  const fetchAbout = useRef(false)
  !showArticles && (fetchAbout.current = true)
  const {togglePopup} = useContext(BrowserContext)
  const {translate} = useContext(TranslationContext)
  return (
    !error ? 
      <>
        <UserInfo setError={setError} setLoading={setLoading} />
        <div className='d-flex'>
          <button onClick={()=>{setShowArticles(true); togglePopup()}} className={`col-6 text-center py-2 bg-color-white${ showArticles ? ' bg-color-dark-white' : ''}`}>{translate('Articles')}</button>
          <button onClick={()=>{setShowArticles(false); togglePopup()}} className={`col-6 text-center py-2 bg-color-white${ !showArticles ? ' bg-color-dark-white' : ''}`}>{translate('About')}</button>
        </div>
        { <div hidden={!showArticles}><UserArticles error={error} loading={loading}/></div>}
        { fetchAbout.current && <div hidden={showArticles}>{UserAbout}</div>}
      </>:
        <Error404 />
  )
    
    
}

export default ProfilePage