import React, { useContext, useEffect, useState } from 'react'
import ArticleCard2 from '../../Components/ArticleCard2'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import ArticleCard2Loading from '../../Components/ArticleCard2Loading'
import { useRef } from 'react'
import { TranslationContext } from '../../store/TranslationContext'

const UserArticles = ({error, loading}) => {
    const [errorLoadingArticles, setErrorLoadingArticles] = useState(null)
    const [articlesLoading, setArticlesLoading] = useState(true)
    const [data, setData] = useState([])
    const username = useParams().username.replace('@', '')
    const seenArticles = useRef([])
    const isNext = useRef(false)
    const loadArticles = ()=>{
      axios.post(`/api/user/profile/${username}/get-articles/`,
        {'seen-articles' : JSON.stringify(seenArticles.current)},
        {
          headers : {
            "Content-Type": "multipart/form-data",
          }
        })
        .then((response)=>{
          setArticlesLoading(false)
          isNext.current = response.data[1]
          setData(prevState=>[...prevState, ...response.data[0]])
        })
        .catch((error)=>{
          setArticlesLoading(false)
          setErrorLoadingArticles(error)
        })
    }
    useEffect(()=>{
      if(!loading && !error) {
        loadArticles()
      }if(!loading && error){
        isNext.current = false
      }
    }, [error, loading])
  
  let count = 0
  const {translate} = useContext(TranslationContext)
  return (
    <div className='d-flex flex-column'>
      <div className='py-3 d-flex px-1 justify-content-center d-flex flex-wrap'>
        {data && data.map((article)=>{
          count +=1
          seenArticles.current = [...seenArticles.current, article.id]
          return <React.Fragment  key={article.id}>
          <ArticleCard2 link={'/redirect/?redirect=' + article.url} article={article}/>
          </React.Fragment>
        }) }
        {data && data.length == 0 && isNext.current===false && articlesLoading===false && 
            <h4 className='m-3'>{translate('No Articles yet')}</h4>
        }
        {articlesLoading && Array.from({length: 20}, (_, x) => x).map((i)=>{return<ArticleCard2Loading key={i}/>})}
      </div> 
      {isNext.current &&
          <button disabled={articlesLoading} className={`border ${ articlesLoading ? '' : 'border-black'} rounded-circle p-3 mb-3 mx-auto aspect-ratio-1 d-flex align-items-center`} onClick={()=>{setArticlesLoading(true); loadArticles()}}>
            <i className="fa-solid fa-plus fa-2xl"></i>
          </button>
      }
    </div>
    
     
  )
}

export default UserArticles